import { Col, Container, Row } from 'react-bootstrap';
import Album from './Album';
import './Discography.css';


function Discography() {

  return (
    <div className="Discography">
      <Container>
        <Row xs={1} md={2} lg={3}>
          <Col><Album title='<COMING SOON>' subtitle='Digital Single' img='./cover_art/comingsoon.png' 
                      link='https://jessetonic.bandcamp.com/' date='dd MM YYYY'/>
          </Col>
          
          
        </Row>
      </Container>
    </div>
  );
}

export default Discography;
