import './ContentRatings.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
import { Button, Row, Col, Container } from 'react-bootstrap';
import { useEffect } from 'react';


function ContentRatings() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="ContentRatings">
      <MyNavbar active='content_ratings' />
      <animated.div style={fadein_props}>
        <h1 className='Title'>CONTENT RATINGS</h1>

        <p id='introParagraph'>
          Content ratings are present on my content to indicate the approximate age range at
          which a particular content may be suitable or yet too inappropriate for minors of
          a certain age range.
          Click the button below for detailed content rating guidelines.
        </p>

        <Button href="https://docs.google.com/document/d/1mNsA7U5pP_rh_4VNz2k7bnlRoFF89fExP2qTK_ZMR14/edit?usp=sharing"
          target="_blank" rel="noopener noreferrer" id='contentRatingGuidelinesButton'>
          CONTENT RATING GUIDELINES
        </Button><br /><br />

        <Container>
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/03+.png')} alt='03+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 3+</u></h3>
              <p className='ratingSummary'>
                Generally suitable for all ages (3 and up). <br/>
                While not necessarily aimed for young kids, 3+ content should not contain any material that is likely to be
                inappropriate for preschoolers' sense of wellbeing or morals.
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/06+.png')} alt='06+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 6+</u></h3>
              <p className='ratingSummary'>
                May not be suitable for children under 6. <br/>
                Some of the scary and violent imagery or crude behaviour may raise concerns about how the content may impact
                the youngest children, under the age of 6, as they can be sensitive and easily influenced by such material.
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/09+.png')} alt='09+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 9+</u></h3>
              <p className='ratingSummary'>
                May not be suitable for children under 9. <br/>
                Understanding what is realistic or not becomes crucial at 9+ because content with this classification may
                begin to delve into more serious real-life challenges that preadolescents are becoming more aware of.
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/12+.png')} alt='12+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 12+</u></h3>
              <p className='ratingSummary'>
                Not recommended for children under 12. <br/>
                Many children begin to view the world around them with a more grown-up perspective by age 12. Hence, 12+ content
                may be too challenging or distressing to children who have yet to reach adolescence.
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/14+.png')} alt='14+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 14+</u></h3>
              <p className='ratingSummary'>
                Not recommended for children under 14. <br/>
                As teenagers become more familiar with mature themes, it becomes crucial that content they consume from everyday
                media do not pose threats to their sense of wellbeing or morals.
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/16+.png')} alt='16+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 16+</u></h3>
              <p className='ratingSummary'>
                Not recommended for minors under 16. <br/>
                Although age 16 is when teenagers start to be able to act like independent adults in society, they are
                still vulnerable to dangerous effects of consuming mature content in media. <br/>
                <strong><em> No minors under 13 are allowed to access 16+ content. </em></strong>
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/18+.png')} alt='18+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 18+</u></h3>
              <p className='ratingSummary'>
                Suitable for adults 18 and up only. <br/>
                18+ indicates adult content which consists of explicit or extreme material that can disturb adults of any age. <br/>
                <strong><em> No minors under 18 are allowed to access 18+ content. </em></strong>
              </p>
            </Col>
          </Row><br />
          
          {/* <Row>
            <Col style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column', alignItems: 'center' }}>
              <p>Demo of content rating bumpers in my videos</p>
              <iframe id="demo-video" src="https://www.youtube.com/embed/CrP2cPbyRCQ?&vq=hd1080" title="YouTube video player"
                frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                allowfullscreen></iframe>
            </Col>
          </Row> */}
        </Container>

      </animated.div>
    </div>
  );
}

export default ContentRatings;
