import './Music.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
import Discography from './music/Discography';
import { Row, Col } from 'react-bootstrap';
import { useEffect } from 'react';


function Music() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="Music">
      <MyNavbar active='music' />
      <animated.div style={fadein_props}>
        <h1 className='Title'>MUSIC</h1>

        <Row xs={1} md={2} className='MusicPlatformsLinks'>
          <Col><h2>STREAM MY MUSIC</h2></Col>
          <Col className='MusicPlatformButton'>
            <a href="/" target="_blank" rel="noopener noreferrer">
              <i class="fa-regular fa-circle-play" />&nbsp;
            </a>
            <a href="/" target="_blank" rel="noopener noreferrer">
              <i class="fa-brands fa-spotify" />&nbsp;
            </a>
            <a href="/" target="_blank" rel="noopener noreferrer">
              <i class="fa-brands fa-itunes" />&nbsp;
            </a>
            <a href="/" target="_blank" rel="noopener noreferrer">
              <i class="fa-brands fa-amazon" />&nbsp;
            </a>
            <a href="https://jessetonic.bandcamp.com/" target="_blank" rel="noopener noreferrer">
              <i class="fa-brands fa-bandcamp" />&nbsp;
            </a>
            <h4>... and in other platforms supported by DistroKid!</h4>
          </Col>
        </Row>
        <Row xs={1} md={2} className='MusicPlatformsLinks'>
          <Col><h4>Music videos, covers, arrangements, trailers, and more! </h4></Col>
          <Col className='MusicPlatformButton'>
            <a href="https://www.youtube.com/@jessetonic" target="_blank" rel="noopener noreferrer">
              <i class="fa-brands fa-youtube" />&nbsp;
            </a>
            <a href="https://soundcloud.com/jessetonic" target="_blank" rel="noopener noreferrer">
              <i class="fa-brands fa-soundcloud" />&nbsp;
            </a>
          </Col>

        </Row>
        <br />

        <hr style={{ color: 'whitesmoke', margin: '1vw' }}></hr>

        <br />
        <h2>DISCOGRAPHY</h2>
        <h4>Click on each release to learn more about it on Bandcamp!</h4>
        <Discography />

      </animated.div>
    </div>
  );
}

export default Music;
