import './About.css';
import MyNavbar from '../MyNavbar';
import { Col, Container, Row } from 'react-bootstrap';
import { animated, useSpring } from '@react-spring/web';
import { useEffect } from 'react';


function About() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  const discord_coming_soon = () => {
    alert("Jesse's Fox Den community Discord server is not yet open at the moment. Please be patient! >w<");
  }

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="About">
      <MyNavbar active='about' />
      <animated.div style={fadein_props}>

        <h1 className='Title'>About</h1>
        <Container>

          <Row className='biography' xs={1} md={2}>
            <Col style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
              <img src={require('./about/20221224 jesse_painting.png')} style={{ width: '75%' }} alt='profile' />
              <a href='https://www.stardustfox.com' target="_blank" rel="noopener noreferrer"
                style={{ color: 'aqua' }}>
                Art by StardustFoxArt
              </a>
            </Col>
            <Col>
              <h1>Jesse Tonic</h1>
              <h4>(22, he/they)</h4>
              <br />
              <p>
                Hello! My name is Jesse Tonic. I am a hobbyist musician, artist, and content creator.
                As I was playing clarinet, I began developing an interest in composing orchestral,
                instrumental, soundtrack music and wrote my first piece by age 13. Around the same time, I
                also experimented with being a YouTuber and taught myself video recording and editing. Later,
                I have become invested in drawing, especially in character art, as well. From this interest
                came to life the pink fox persona, Jesse Tonic. In real life, I am currently a
                university student studying computer science.
              </p>
            </Col>
          </Row>

          <br /><hr style={{ color: 'whitesmoke', margin: '1vw' }}></hr><br />

          <Row xs={1} md={2}>
            <Col><h1>Social Media Links</h1></Col>
            <Col className='SocialMedia'>
              <a href="https://www.youtube.com/@jessetonic" target="_blank" rel="noopener noreferrer">
                <i class="fa-brands fa-youtube" />
              </a>
              <a href="https://www.instagram.com/jessetonic/" target="_blank" rel="noopener noreferrer">
                <i class="fa-brands fa-instagram" />
              </a>
              <a href="https://mastodon.art/@jessetonic" target="_blank" rel="noopener noreferrer">
                <i class="fa-brands fa-mastodon" />
              </a>
              <a href="https://www.tumblr.com/jessetonic" target="_blank" rel="noopener noreferrer">
                <i class="fa-brands fa-tumblr" />
              </a>
              <a href="https://twitter.com/JesseTonicFox" target="_blank" rel="noopener noreferrer">
                <i class="fa-brands fa-twitter" />
              </a>
              <a href="https://www.tiktok.com/@jessetonic" target="_blank" rel="noopener noreferrer">
                <i class="fa-brands fa-tiktok" />
              </a>
              <a href="https://www.twitch.tv/jessetonic" target="_blank" rel="noopener noreferrer">
                <i class="fa-brands fa-twitch" />
              </a>
              <i class="fa-brands fa-discord" onClick={discord_coming_soon} />
            </Col>
          </Row>

          <br /><br />

          <Row style={{ fontSize: '125%' }}>
            <ul>
              <li>
                This YouTube link refers to my main music channel. For my Downtime W/ Jesse channel, please
                check the DOWNTIME W/ JESSE tab.
              </li>
              <li>
                My Twitch is mainly for Downtime W/ Jesse as well. However, I may do music or art streams as
                well, but this is pending as of right now.
              </li>
            </ul>
          </Row>

        </Container>
      </animated.div>
    </div>
  );
}

export default About;
